export default {
  data() {
    return {
      list: [{
        storeName: '优逸smilehome自营',
        goodArr: []
      }],
      total_price: 0,         // 总计算钱数
      checked_sum: 0,         // 选中商品数量
      all_checked: false,     // 全部选中
      goodId: [],             // item.goods_id + '_' + item.goods_sku_id
      isEdit: false,          // 是否处于编辑状态
    }
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  onLoad() {
    // //  获取购物车数据
    // this.getList()
  },
  onShow() {
    this.checked_sum = 0
    //  获取购物车数据
    
    this.getList()
    if(this.all_checked) {
      this.selAllRadio()
    }
  },
  methods: {
    // 获取购物车列表
    getList() {
      let that = this
      that.$http({
        url: that.$api.shopcarList,
        cb: (err, res) => {
          let list =  res.data.goods_list
          list.map((item, index) => {
            console.log(item)
            item.checked = false
          })
          console.log(list)
          this.list[0].goodArr = list
        }
      })
    },
    // 去结算
    toSureOrder() {
      uni.navigateTo({
        url: '../order/submitOrder?cart=true&cart_ids=' + this.goodId.join(',')
      })
    },
    // 勾选商品
    checkboxChange(id, num, index) {
      // console.log(id, num, index)
      let checked = this.list[index].goodArr[num].checked
      // console.log(this.list[index].goodArr[num].checked)
      this.list[index].goodArr[num].checked = !checked
      // 计算总金额
      this.computePrice()
    },
    
    delGood() {
      let that = this
      uni.showModal({
        content: '确认删除所选商品？',
        success(res) {
          if (res.confirm) {
            that.$http({
              url: that.$api.delcar,
              data: {
                goods_sku_id: that.goodId.join(',')
              },
              cb: (err, res) => {
                if(!err && res.code === 1) {
                  uni.showToast({
                    title: '删除成功',
                    icon: 'none'
                  })
                  // 重新获取列表
                  that.getList()
                } else {
                  uni.showToast({
                    title: '删除失败请重试',
                    icon: 'none'
                  })
                }
              }
            })
          } else if (res.cancel) {
            console.log('用户点击取消')
          }
        }
      })
    },
    
    // 计算总金额
    computePrice() {
      let that = this
      let list =  this.list[0].goodArr
      that.total_price = 0
      that.checked_sum = 0
      that.goodId = []
      list.map((item, index) => {
        // console.log(item.checked, item.total_num, that.checked_sum, that.total_price)
        if(item.checked) {
          that.checked_sum += Number(item.total_num)
          that.total_price += Number(item.goods_price) * Number(item.total_num)
          that.goodId.push(item.goods_id + '_' + item.goods_sku_id)
        }
      })
    },
    
    // 选中所有商品
    selAllRadio() {
      let that = this
      if(that.all_checked) {
        that.list[0].goodArr.map((item, index) => {
          item.checked = false
        })
      } else {
        that.list[0].goodArr.map((item, index) => {
          item.checked = true
        })
      }
      this.all_checked = !this.all_checked
      
      // 计算总金额
      this.computePrice()
      
    },
    
    // 加减购物车物品数量
    controlNum(good, index, type) {
      let that = this
      let url = that.$api.addcar
      let num = that.list[0].goodArr[index].total_num
      let data = {
        goods_id: good.goods_id,
        goods_sku_id: good.goods_sku_id
      }
      
      // 减少数量
      if (type === 'cut' && num > 1) {
        url = that.$api.subcar
        that.$http({
          url: url,
          methods: 'POST',
          data: data,
          cb: (err, res) => {
            if (!err && res.code === 1) {
              that.list[0].goodArr[index].total_num -= 1
              
              // 计算总金额
              this.computePrice()
              return
            }
          }
        })
      }
      
      // 增加数量
      if (type === 'add') {
        data.goods_num = 1
        that.$http({
          url: url,
          methods: 'POST',
          data: data,
          cb: (err, res) => {
            if (!err && res.code === 1) {
              that.list[0].goodArr[index].total_num = that.list[0].goodArr[index].total_num - 0 + 1
              
              // 计算总金额
              this.computePrice()
              return
            } else if (res.code === 0) {
              uni.showToast({
                title: res.msg,
                icon: 'none'
              })
            }
          }
        })
        return
      }
    },

    goShopping() {
      uni.switchTab({
        url: '../market/market'
      })
    },
    goDetail(goods_id, type) {
      if(type === 0) {
        uni.navigateTo({
          url: '../components/goodDetail?goods_id=' + goods_id
        })  
      } else if(type === 1) {
        uni.navigateTo({
          url: '../components/goodDetail?goods_id=' + goods_id + '&panic=true&title=' + '秒杀购' 
        })
      } else if(type === 2) {
        uni.navigateTo({
          url: '../components/goodDetail?goods_id=' + goods_id + '&panic=true&title=' + '限时购' 
        })
      }
    }
  }
}
